<template>
  <div class="mb-10 leaderboard">
    <div class="my-5">
      <h1 class="mb-4 text-2xl font-bold text-center dark:text-white">
        Leaderboard-{{ this.$route.params.id }}
      </h1>
      <div
        class="p-5 mx-1 bg-gray-200 rounded md:w-1/2 md:mx-auto bg-opacity-60"
      >
        <div>
          <input
            v-model="search"
            placeholder="Search by Name/College"
            @keyup="searchResult"
            class="
              block
              w-full
              py-1.5
              px-3
              text-gray-800
              border border-gray-300
              transition
              duration-500
              focus:outline-none
              focus:border-black
              rounded
            "
          />
        </div>
        <div class="mt-4">
          <div v-if="allresult.length > 0">
            <div
              v-for="(r, i) in testResults"
              :key="i"
              :id="r.roll"
              :class="`${getborderStyle(
                r.position
              )}  flex justify-between items-center p-5 rounded bg-gray-100 dark:bg-gray-900 dark:text-white shadow-md`"
            >
              <span
                class="flex items-center justify-center w-10 h-10 font-semibold text-white rounded-full  bg-gradient-to-t from-red-400 to-red-600"
              >
                {{ r.position }}
              </span>
              <div class="flex-1 ml-2 md:ml-4">
                <h2 class="text-lg font-semibold">
                  {{ r.name }}
                </h2>
                <p>{{ r.roll }}</p>
                <p>{{ r.duration }} minutes</p>
              </div>
              <div class="p-2 border-2 border-red-400">
                {{ r.score }}
              </div>
            </div>
          </div>
          <div
            v-else
            class="flex items-center justify-center mt-10 space-x-1 text-sm text-gray-700  dark:text-white"
          >
            <svg
              fill="none"
              class="w-10 h-10 animate-spin"
              viewBox="0 0 32 32"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                clip-rule="evenodd"
                d="M15.165 8.53a.5.5 0 01-.404.58A7 7 0 1023 16a.5.5 0 011 0 8 8 0 11-9.416-7.874.5.5 0 01.58.404z"
                fill="currentColor"
                fill-rule="evenodd"
              />
            </svg>

            <div class="text-lg">Loading ...</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { routine, exams } from "@/plugins/auth.js";
import { mapState } from "vuex";
export default {
  data() {
    return {
      search: "",
      testResults: [],
      allresult: [],
      results: [],
    };
  },

  methods: {
    async modeltestResults() {
      let cres = await fetch(`${routine}&id=${this.$route.params.id}`);
      let cexm = await cres.json();
      let rres = await fetch(exams + atob(cexm.exam.mcqsheetlink));
      let rdata = await rres.json();
      this.testResults = rdata
        .sort((c, d) => c.duration - d.duration)
        .sort((a, b) => b.score - a.score)
        .map((r, i) => ({
          position: i + 1,
          name: r.name,
          roll: r.roll,
          duration: this.millisToMinutesAndSeconds(
            r.duration <= 1500000 ? r.duration : 1500000
          ),
          score: r.score,
        }));

      this.allresult = this.testResults;

      setTimeout(() => {
        location.hash = `#${this.user.roll}`;
      }, 2000);
    },
    millisToMinutesAndSeconds(millis) {
      var minutes = Math.floor(millis / 60000);
      var seconds = ((millis % 60000) / 1000).toFixed(0);
      return minutes + ":" + (seconds < 10 ? "0" : "") + seconds;
    },

    searchResult() {
      this.testResults = this.allresult.filter((r) => {
        if (
          r.roll.toLowerCase().includes(this.search.toLowerCase()) ||
          r.name.toLowerCase().includes(this.search.toLowerCase())
        ) {
          return r;
        }
      });
    },
    getStyle(p) {
      if (p == 1) {
        return "success";
      } else if (p == 2) {
        return "orange darken-2";
      } else if (p == 3) {
        return "indigo";
      }
      return "";
    },
    getborderStyle(p) {
      if (p == 1) {
        return "s singleResult mb-2";
      } else if (p == 2) {
        return "f singleResult mb-2";
      } else if (p == 3) {
        return "d singleResult mb-2";
      }
      return "singleResult mb-2";
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  created() {
    this.modeltestResults();
  },
};
</script>

<style lang="scss" scoped>
.leaderboard {
  background-attachment: fixed;
  min-height: 100vh;
  .resultcontainer {
    background: rgba(223, 221, 221, 0.747) !important;
    box-shadow: 0 8px 32px 0 rgba(12, 13, 32, 0.479);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    .singleResult {
      background: rgba(255, 255, 255, 0.35);
      box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
      backdrop-filter: blur(18.5px);
      -webkit-backdrop-filter: blur(18.5px);
      border-radius: 10px;
      border: 1px solid rgba(255, 255, 255, 0.18);
    }
  }
}

.s {
  border: 2px solid rgba(22, 223, 49, 0.808) !important;
}
.f {
  border: 2px solid rgba(231, 154, 10, 0.808) !important;
}
.d {
  border: 2px solid rgba(23, 110, 192, 0.808) !important;
}
.totalmarks {
  border: 2px solid rgb(236, 25, 88) !important;
  height: 35px;
  width: 35px;
  padding: 3px;
  box-shadow: 2px 2px 4px rgba(236, 25, 88, 0.8);
}
</style>